import React from 'react';

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));
const RegisterAdmin = React.lazy(()=>import('./views/registeradmin'));
const ViewAdmin = React.lazy(()=>import('./views/viewadmins'));
const ChangePassword = React.lazy(()=>import('./views/change-password'));
const ViewUsers = React.lazy(()=>import('./views/view-users'));
const ViewUser = React.lazy(()=>import('./views/view-user'));
const ViewBankDetails = React.lazy(()=>import('./views/view-bank-details'));
const ViewEventDetails = React.lazy(()=>import('./views/view-events'));
const ViewTransactionDetails = React.lazy(()=>import('./views/view-transaction-details'));
const PaidFansList = React.lazy(()=>import('./views/view-fans-paid'));
const TransactionCharge = React.lazy(()=>import('./views/view-transaction-charge'));



const routes = [
  { path: '/', exact: true, name: 'Home' },
  {path:'/register-admin',exact:true,'name':"Register Admin",component:RegisterAdmin},
   {path:'/view-admin',exact:true,name:"View Admin",component:ViewAdmin},
   {path:'/change-password',exact:true,name:"Change Password",component:ChangePassword},
   {path:'/view-users',exact:true,name:"View Users",component:ViewUsers},
   {path:'/view-user/:id',exact:true,name:"View User",component:ViewUser},
   {path:'/view-bank-details',exact:true,name:"View BankDetails",component:ViewBankDetails},
   {path:'/view-events',exact:true,name:"View Events",component:ViewEventDetails},
   {path:'/paid-fans/:id',exact:true,name:"View Paid Fans For This Event",component:PaidFansList},
   {path:'/view-transaction-details',exact:true,name:'View Transactions',component:ViewTransactionDetails},
   {path:'/set-transaction-charge',exact:true,name:"Set Percentage Charge",component:TransactionCharge},
   {path: '/dashboard', name: 'Dashboard', component: Dashboard },

  
];

export default routes;
