import HttpService from './HttpService';
import Cookies from 'js-cookie'

///number-of-events
export const totalNumberOfEvents = () =>
{
    const http = new HttpService();
    
   let addedUrl = "admin/number-of-events";
  
    let tokenId = 'vt-music';
    return http.getData(addedUrl,tokenId).then((data)=>
    {
        console.log(data)
        return data;

    }).catch((error)=>
    {
        console.log(error)
        return error;
    });
} 

