import React,{useEffect, useState } from 'react'
import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CProgress
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { NavLink } from 'react-router-dom'
import { totalNumberOfaUnapprovedCelebs,
   totalNumberOfFailedTransactions } from '../services/DashboardDetailsService'


const TheHeaderDropdownNotif = () => {
 
 

  const [failedTransactionsCount,setFailedTransactionsCount ] = useState(0);

  const [celebritiesUnapproved,setCelebritiesUnapproved] = useState(0);

  useEffect(() => {

   totalFailedTransCount();
   totalUnapprovedCelebsCount();
   

    return () => {
   
    }
  }, [])


  const totalFailedTransCount = () =>
  {
    totalNumberOfFailedTransactions().then((response)=>
    {
      console.log(response);
      setFailedTransactionsCount(response.data);
    });
  }

  const totalUnapprovedCelebsCount = () =>
  {
    totalNumberOfaUnapprovedCelebs().then((response)=>
    {
      setCelebritiesUnapproved(response.data);
    });
  } 

  const returnTotal = (first,second)=>
  {
    let total = parseInt(first) + parseInt(second);
    return total;
    
  }

  return (
    <CDropdown
      inNav
      className="c-header-nav-item mx-2"
    >
      <CDropdownToggle className="c-header-nav-link" 
      caret={false}
      >
        <CIcon name="cil-bell"/>
         <CBadge shape="pill" color="danger">
           {

          returnTotal(failedTransactionsCount,celebritiesUnapproved)==0?
          ""
          :
          returnTotal(failedTransactionsCount,celebritiesUnapproved)
         }</CBadge>      
      </CDropdownToggle>

     
     
      {
          returnTotal(failedTransactionsCount,celebritiesUnapproved)==0?
          ""
          :
          <CDropdownMenu  placement="bottom-end" className="pt-0">
       
        
       {
         failedTransactionsCount==0 || failedTransactionsCount == null?
         ""
         :
         <CDropdownItem className="d-block">
          
         <NavLink to="/view-transaction-details">
         <div className="text-uppercase mb-1">
         <small><b>{failedTransactionsCount} Failed Transactions</b></small>
       </div> 
         </NavLink>
 
     </CDropdownItem> 

       }

       
       {
          celebritiesUnapproved==0 || celebritiesUnapproved==null?
          ""
          :
          <CDropdownItem className="d-block">
          <div className="text-uppercase mb-1">
  <small><b>{celebritiesUnapproved} New Celebrities To Be Approved</b></small>
          </div>    
        </CDropdownItem>
       }
        
        
      </CDropdownMenu>
         }

      


    </CDropdown>
  )
}

export default TheHeaderDropdownNotif